*{
  margin: 0;
  padding: 0;
}

.screen{
  background-color: #F1F1F1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100vw;
  line-height: 5rem;
}
#section{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#searchText{
  font-family: 'Bad Script', cursive;
  font-size: 3rem;
  font-family: 'Monoton', cursive;
}
.searchAndBtn{
  display: flex;
  flex-direction: row;
}
#searchBox{
  position: relative;
  width: 35vw;
  height: 8vh;
  border-top-left-radius: 10rem;
  border-bottom-left-radius: 10rem;
  outline: none;
  border: 1px solid gray;
  padding-left: 3rem;
  padding-right: 1rem;
  font-size: 2rem;
  font-weight: bold;
  font-family: 'Gruppo', cursive;
  
}

#searchBtn{
  text-decoration: none;
   display: flex;
   justify-content: center;
   align-items: center;
   font-family: 'Gruppo', cursive;
   font-weight: bold;
   font-size: 1.3rem;
     width: 7vw;
     height: 8vh;
     border: none;
     border-top-right-radius: 10rem;
     border-bottom-right-radius: 10rem;
     border-top: 1px solid gray;
     border-bottom: 1px solid gray;
     border-right: 1px solid gray;
     background-color: white;
     color: gray;
     box-shadow: -1px 0px 4px 0px;
     cursor: pointer;
     
    }
    #searchBtn:active{
      box-shadow:inset 0px 0px 4px 0px;
    }

    .footer{
      position: fixed;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      background-color:darkgray;
      width: 100vw;
      height: 5vh;
      font-family: 'Gruppo', cursive;
      font-size: 2rem;
      color: white;
      word-spacing: 1rem;
      bottom: 0;
      left: 0;
      box-shadow: 0px 0px 5px 0px black;
    
    }
    #searchBox::-webkit-search-cancel-button{
      position:absolute;
      left: 2%;  
      top:50%;
      transform: translateY(-15%);
      -webkit-appearance:none;
      height: 5rem;
      width: 5rem;
      background-image: url("./trash.svg");
      background-repeat: no-repeat;
      background-size: 30%;
      cursor: pointer;
  }
  #news{
    position: absolute;
    top: 3%;
    left: 1%;
    width: 25vw;
    height: 80vh;
    box-shadow: 0px 0px 3px 0px;
  }

#S{color: #7D1E6A;animation: S infinite forwards ease-in-out 1s;}
#A{color: #0F3D3E;animation: A infinite forwards ease-in-out 1s;}
#C{color: #774360;animation: C infinite forwards ease-in-out 1s;}
#H{color: #3BACB6;animation: H infinite forwards ease-in-out 1s;}
.E{color: #31087B;animation: E infinite forwards ease-in-out 1s;}
.R{color: #C21010;animation: R infinite forwards ease-in-out 1s;}
@keyframes S {0%{color: #7D1E6A;}100%{color: #3BACB6;}}
@keyframes E {0%{color: #31087B;}100%{color: #0F3D3E;}}
@keyframes A {0%{color: #0F3D3E;}100%{color: #31087B;}}
@keyframes R {0%{color: #C21010;}100%{color: #774360;}}
@keyframes C {0%{color: #774360;}100%{color: #C21010;}}
@keyframes H {0%{color: #3BACB6;}100%{color: #7D1E6A;}}



@media only screen and (max-width:480px){
 #section{
  position: absolute;
  top: 5%;
 }
  #searchText{
    font-size: 2rem;
  }
  #searchBox{
    font-size: 1rem;
    padding-right: 1rem;
    min-height: 2rem;
    max-height: 2rem;
    width: 80vw;
  }
  #searchBtn{
    height: 2rem;
    font-size: 0.6rem;
    width: 15vw;
  }
  .footer{
    bottom: 0;
    z-index: 999;
    font-size: 1rem;
    height: 4vh;
  }
  #searchBox::-webkit-search-cancel-button{
    position:absolute;
    left: 2%;  
    top:50%;
    transform: translateY(-15%);
    -webkit-appearance:none;
    height: 5rem;
    width: 5rem;
    background-image: url("./trash.svg");
    background-repeat: no-repeat;
    background-size: 30%;
    cursor: pointer;
}

#news{
  position: absolute;
  top: 13rem;
  

  width: 98vw;
  height: 100vh;
}


}
@media only screen and (min-device-width: 481px) 
                   and (max-device-width: 680px) 
                   and (orientation: landscape) {

#searchBox{
  font-size: 1rem;
  width: 60vw;
  min-height: 2rem;
}
#searchBtn{
min-height: 2rem;
width: 15vw;
font-size: 1rem;
}
.footer{
  font-size: 1rem;
}
#news{
  display: none;
}

}